<template>
    <div class="binding">
        <header class="both">
            <div class="phone" @click="routerToPhone"><div class="phone-text"><span >手机绑定</span></div></div>
            <div class="phone"><div class="email-text"><span class="phone-line">邮箱绑定</span></div></div>
        </header>
        <main>
            <div class="bindTips"><img class="icon" src="../assets/images/horn.png" alt="">绑定手机号/邮箱可获得20颗逆袭豆</div>
            <div class="mgt-15"><span class="input-phone">邮 箱：</span> <input class="input" type="text" placeholder="输入邮箱"></div>
            <div class="code">
                <span class="input-phone">验 证：</span>
                <input class="input" type="number" oninput="if(value.length>6)value=value.slice(0,6)" placeholder="输入邮箱验证码">
                <van-button v-bind:disabled="dis" @click="getCode" class="getCode" size="mini"><span  v-if="show" >获取验证码</span>
                    <span v-if="!show" style="color: white">{{count}} s</span></van-button>
            </div>
            <div class="bindBtn"><img @click="bind" src="../assets/images/bindBtn.png" alt="" width="115px"></div>
        </main>
    </div>
</template>

<script>
    export default {
        name: "emailBinding",
        data(){
            return{
                show:true,
                count:"",
                dis:false
            }
        },
        methods:{
            routerToPhone() {
                this.$router.push({name: 'phoneBinding'});
            },
            bind:function () {
                this.$router.push({name: 'bindSuccess'});
            },
            getCode() {
                this.$toast('验证码已发送');
                this.count = 10;
                this.dis = true;
                // if (!this.timer) {
                //     this.count = this.count;
                this.show = false;
                this.timer = setInterval(() => {
                    if (this.count > 0 && this.count <= this.count) {
                        this.count--;
                    } else {
                        this.show = true;
                        this.dis = false;
                        clearInterval(this.timer);
                        this.timer = null;
                    }
                }, 1000)
                // }
            },
        },
    }
</script>

<style scoped>
    @import "../assets/css/bind.css";
    .getCode{
        position: absolute;
        right: 8px;
        top: 4px;
        width: 65px;
        color: white;
        background: #fcd32d;
        border: none;
    }
    .code .van-button--disabled{
        opacity: 1;
    }
</style>