<template>
    <div class="card">
        <div class="card-bg">
            <div class="card-content">
                <p>学生卡</p>
                <div class="card-detail">
                    <div class="photo-content">
                        <img src="../assets/images/1.jpg" alt="" class="card-photo">
                    </div>
                    <div class="group">
                        <span class="fc-fcb">姓名：<input class="editor-input fc-5f2" type="text" v-model="name"></span><br>
                        <span class="fc-fcb">年级：<span @click="pop" class="grade fc-5f2" >{{grade}}</span></span><br><br>
                        <span class="fc-fcb">逆袭豆：<span class="fc-5f2">{{beans}}</span> </span><br>
                        <span class="fc-fcb">年级排名：<span class="fc-5f2"></span>宇宙第一</span>
                    </div>
                </div>
            </div>
        </div>
        <div class="saveBtn"><img src="../assets/images/saveBtn.png" width="110px" alt=""></div>
        <van-popup v-model="show" position="bottom" >
            <van-picker
                    show-toolbar
                    title=""
                    :columns="columns"
                    @cancel="onCancel"
                    @confirm="onConfirm"></van-picker>
        </van-popup>

    </div>
</template>

<script>
    export default {
        name: "Editor",
        data(){
            return{
                columns: ['初一', '初二', '初三', '高一', '高二','高三'],
                show:false,
                grade:'',
                name:'',
                beans:'',
            }
        },
        methods: {
            pop(){
                this.show=true;
            },
            onConfirm(value) {
                this.grade=value;
                this.show=false;
            },
            onCancel() {
                this.show=false;
            }
        }
    }
</script>

<style scoped>
    @import "../assets/css/studentCard.css";
    .editor-input ,.grade{
        width: 54%;
        border-radius: 6px;
        background: #fcd32d;
        font-size: 1rem;
        border: none;
        line-height: 1.2rem;
        padding-left: 10px;
        /*letter-spacing: 2px;*/
    }
    .grade{
        vertical-align: middle;
        display: inline-block;
        height: 19px;
    }
    .saveBtn{
        margin-top: 35px;
        text-align: center;
    }
</style>